@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

.container {
  padding-left: 10px;
  padding-right: 10px;
}

.row {
  margin-left: -10px;
  margin-right: -10px;
}

.row [class^="col-"] {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

@media (min-width: 860px) {
  .row {
    display: flex;
  }
  .row .col-1-12 {
    width: 8.33333%;
  }
  .row .col-2-12 {
    width: 16.66667%;
  }
  .row .col-3-12 {
    width: 25%;
  }
  .row .col-4-12 {
    width: 33.33333%;
  }
  .row .col-5-12 {
    width: 41.66667%;
  }
  .row .col-6-12 {
    width: 50%;
  }
  .row .col-7-12 {
    width: 58.33333%;
  }
  .row .col-8-12 {
    width: 66.66667%;
  }
  .row .col-9-12 {
    width: 75%;
  }
  .row .col-10-12 {
    width: 83.33333%;
  }
  .row .col-11-12 {
    width: 91.66667%;
  }
  .row .col-12-12 {
    width: 100%;
  }
}

/* ogólne */
body {
  font-family: "Lato", sans-serif;
}

.main-block {
  background: #d2d0ee;
  background: linear-gradient(90deg, #d2d0ee 0%, #ffdfdf 35%, #3396aa 100%);
}

/* stylowanie headera */
.header {
  background: #fff;
  padding: 0 40px;
  border-bottom: 1px solid #000;
}

.nav {
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  cursor: pointer;
}

.nav__title {
  font-size: 30px;
}

.menu-list {
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.menu-list__element {
  cursor: pointer;
}

@media (max-width: 800px) {
  .menu-list {
    width: 150px;
  }
}

/* stylowanie sekcji dotyczącej dodawania nowego zadania */
.app-add-block {
  width: 800px;
  margin: 0 auto;
}

.app-add-block__title {
  text-align: center;
  font-size: 40px;
  padding: 40px 0;
  font-weight: 700;
}

.app-add-block__tools {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-bottom: 30px;
}

.input-task {
  width: 400px;
  padding: 5px 0 5px 10px;
  height: 40px;
  border-radius: 15px;
  border: 2px solid #000;
}

.select-css {
  width: 100px;
  height: 54px;
  padding-left: 15px;
  border-radius: 15px;
  border: 2px solid #000;
}

#add-element {
  width: 150px;
  height: 54px;
  border-radius: 15px;
  border: 2px solid #000;
  background-color: #fff;
  font-weight: bold;
}

@media (max-width: 800px) {
  .app-add-block__tools {
    flex-direction: column;
    align-items: center;
  }
  .app-add-block {
    width: 100%;
  }
  .select-css {
    width: 300px;
    padding: 5px 0 5px 10px;
    margin-top: 10px;
  }
  #add-element {
    margin-top: 10px;
  }
}

/* stylowanie bloków z zadaniami */
.block-category {
  display: flex;
  width: 100%;
  align-content: stretch;
}

.block-category-task {
  width: 33.3%;
  border-radius: 8px;
  margin: 20px;
  padding: 20px;
  min-height: 400px;
  max-height: 400px;
  overflow: auto;
  background: #eeedfa;
  -webkit-box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
}

.block-category-task::-webkit-scrollbar {
  width: 11px;
}

.block-category-task::-webkit-scrollbar-thumb {
  outline: 1px solid black;
  border-radius: 13px;
  border: 1px solid #272b2c;
  background-color: #FFF;
  margin-left: 2px;
  background: linear-gradient(90deg, #d2d0ee 0%, #ffdfdf 35%);
}

.block-category-task__title {
  font-size: 26px;
  text-align: center;
  padding-bottom: 20px;
  text-transform: uppercase;
  font-weight: bold;
  position: relative;
  margin-bottom: 30px;
}

.block-category-task__title:before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  height: 1px;
  width: 50%;
  /* or 100px */
  border-bottom: 1px solid black;
  transform: translate(-50%);
}

.remove:hover {
  cursor: pointer;
}

li:hover :hover {
  background: rgba(99, 86, 86, 0.3);
}

.item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  padding: 15px 20px;
  -webkit-box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.75);
  background: #efeefc;
  border-radius: 5px;
  margin-bottom: 20px;
}

.fa-trash:before {
  padding-left: 20px;
  font-size: 20px;
}

.fa-trash:hover {
  color: red;
}

.input-task--error::-webkit-input-placeholder {
  color: red;
}

@media (max-width: 900px) {
  .block-category {
    flex-direction: column;
  }
  .block-category-task {
    width: auto;
  }
}
